.modalContainer {
	position: fixed;
	width: 100%;
	height: 100%;
	z-index: 999;
	justify-content: center;
	top: 0;
	left: 0;
	display: flex;
	align-items: center;
}
.modalInnerContainer {
	border: 2px solid black;
	border-radius: 6px;
	background-color: yellow;
}
.modalContent {
	position: relative;
}
.display-none {
	display: none;
}
